    <li>
    <img src="../../assets/user/info_icon_about@2x.png" class="leftImg" style="width: 0.32rem;height: 0.28rem;margin-right: 0.28rem;">
      <span>关于我们</span>
    </li>
  </ul>
</template>

<script>
export default {
  name: "userListComponent",

};
</script>

<style scoped lang="less">
  #aboutList{
    width: 100%;
    box-sizing: border-box;
    li{
      height: unit(.43*2,rem);
      line-height: unit(.43*2,rem);
      border-bottom: 1px solid #F6F7F8;
      font-size: unit(0.14*2,rem);
      color: #272727;
      background: url('../../assets/user/rightBtn.png') no-repeat unit(3.56*2,rem) center;
      background-size: unit(0.04*2,rem) unit(0.1*2,rem);
      color: #272727;
      .leftImg{
        float: left;
        width: unit(0.15*2,rem);
        height: unit(0.15*2,rem);
        margin: unit(0.15*2,rem) unit(0.15*2,rem) 0 unit(0.15*2,rem);
      }
    }
  }
</style>


